.sidebar {
    margin: 10px;
    background-color: white;
    padding: 10px 0;
    border-radius: 10px;
    border: 1px solid #D7D7D7;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-weight: bolder;
}

.sidebar .ant-menu-item {
    margin-bottom: 20px;
}

.sidebar .ant-menu-item:last-child {
    margin-bottom: 0;
}

.sidebar img {
    margin-bottom: 20px;
}

.container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 1rem;
}

.container img {
    filter: blue;
}

.container p {
    color: #606060;
}

.container a {
    color: #009DFF;
}

.heading {
    padding-top: 15px;
    padding-left: 15px;
    font-size: 36px;
}

* {
    font-family: 'Inter', sans-serif;
}
